let mappers = {
  operation: (op)=>{
    if(op.comment){
      if(op.comment.operationName == 'welcomeBonus'){
        op.name = localizeFilter('operation_welcome_bonus'); // Дивиденды по маркетингу
      } else if(op.comment.operationName == 'referralHotBonus'){
        op.name = localizeFilter('operation_referral_hot_bonus', [op.comment.referralIndexNumber]); // Дивиденды по маркетингу
        op.referralIndexNumber = op.comment.referralIndexNumber;
      } else if(op.comment.operationName == 'liquidityPoolsBalanceWithdraw'){
        op.name = localizeFilter('operation_liquidity_providing_withdraw'); // Дивиденды по маркетингу
      } else if(op.comment.operationName == 'goalsBalanceWithdraw'){
        op.name = localizeFilter('operation_goals_withdraw'); // Дивиденды по маркетингу
      } else if (op.comment.operationName === 'getFenixFromBurnedStake') {
        op.name = localizeFilter('operation_getFenixFromBurnedStake'); // Стейкинг
      } else if (op.comment.operationName === 'stakeDeposit') {
        op.name = localizeFilter('staking_operation_stake'); // Стейкинг
      } else if (op.comment.operationName == 'buyCoinAuctionCommissionFee'){
        op.name = localizeFilter('operation_buy_coin_auction_commission_fee'); 
      } else if (op.comment.operationName == 'buyCoinAuctionSendMoney'){
        op.name = localizeFilter('operation_buy_coin_auction_send_money'); 
      } else if (op.comment.operationName == 'sellCoinAuctionSendCoin'){
        op.name = localizeFilter('operation_sell_coin_auction_send_coin'); 
      } else if (op.comment.operationName == 'sellCoinAuctionReceiveMoney'){
        op.name = localizeFilter('operation_sell_coin_auction_receive_money'); 
      } else if (op.comment.operationName == 'buyCoinAuctionReceiveCoin'){
        op.name = localizeFilter('operation_buy_coin_auction_receive_coin'); 
      }
    } else {
      if(op.type == 'fill' || op.type == 'fill_balance'){
        op.name = localizeFilter('operation_balance_fill'); // Пополнение баланса
      } else if(op.type == 'withdraw' || op.type == 'withdraw_balance'){
        op.name = localizeFilter('operation_balance_withdraw'); // Вывод с баланса
      }
    }
    return op;
  },
  walletOperation: (op)=>{
    // if(op.comment){
    //   if(op.comment.operationName == 'welcomeBonus'){
    //     op.name = localizeFilter('operation_welcome_bonus'); // Дивиденды по маркетингу
    //   } else if(op.comment.operationName == 'referralHotBonus'){
    //     op.name = localizeFilter('operation_referral_hot_bonus', [op.comment.referralIndexNumber]); // Дивиденды по маркетингу
    //     op.referralIndexNumber = op.comment.referralIndexNumber;
    //   }
    // } else {
      op.amount = BigInt(op.amount);
      if(op.type == 'change' || op.amount >= 0n){
        op.name = localizeFilter('operation_balance_fill'); // Пополнение баланса
        op.type='fill'
      } else if(op.type == 'change' || op.amount <= 0n){
        op.name = localizeFilter('operation_balance_withdraw'); // Вывод с баланса
        op.type='withdraw'
      }
    // }
    return op;
  },
  contractObject: (co)=>{
    let getContractObjectByParams = {
      auto: ({mark, model, state, year})=> `${localizeFilter(state)} ${mark} ${model}, ${year}`,
      target: ({product})=>`${product}`,
      realEstate: ({country, city, area, propertyType})=>`${localizeFilter(propertyType)}, ${area} ${localizeFilter('meters_2')}, ${city}, ${country}`,
      closeCredit: ({creditObject})=>`${creditObject}`
    }
    if(co.params){
      co.object = getContractObjectByParams[co.type](co.params);
    }
    return co;
  }
};




