import { api } from '@/plugins/api'
import localizeFilter from '@/filters/localize.filter'
import { wsApi } from '../plugins/wsApi'


export default {
    state: () => ({
        bonusAccountCoinsTickers: ['GDC', 'USDT', 'DRMC', 'CWPT','FENIX'],
    }),
    getters: {
        bonusAccountCoinsTickers: state => {
            return state.bonusAccountCoinsTickers
        },
        bonusAccountCoins: (state, getters) => {
            const res = getters.coins.filter((el)=>{
                return state.bonusAccountCoinsTickers.includes(el.ticker)
            })
            res.splice(1, 0, {name: 'USDT', localizedName: 'USDT', ticker: 'USDT', canBuy: false, canBuyToBonusBalance: false})
            return res
        }
    },
    actions: {
      
    },
    mutations: {

    },
}
