<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { required, helpers, sameAs, minLength, maxLength } from "@vuelidate/validators";
import localizeFilter from '@/filters/localize.filter'
import { api } from "@/plugins/api";


const generateVideoPreview = (file) => {
  return new Promise((resolve) => {
    const canvas = document.createElement("canvas");
    const video = document.createElement("video");

    // this is important
    video.autoplay = true;
    video.muted = true;
    video.src = URL.createObjectURL(file);

    video.onloadeddata = () => {
      let ctx = canvas.getContext("2d");

      canvas.width = video.videoWidth;
      canvas.height = video.videoHeight;

      ctx.drawImage(video, 0, 0, video.videoWidth, video.videoHeight);
      video.pause();
      const dataURL = canvas.toDataURL("image/png");
      // const file = dataURLtoBlob(dataURL);
      return resolve(dataURL);
    };
  });
};

// const dataURLtoBlob = (dataurl) => {
//   var arr = dataurl.split(","),
//     mime = arr[0].match(/:(.*?);/)[1],
//     bstr = atob(arr[1]),
//     n = bstr.length,
//     u8arr = new Uint8Array(n);
//   while (n--) {
//     u8arr[n] = bstr.charCodeAt(n);
//   }
//   return new Blob([u8arr], { type: mime });
// };

export default defineComponent({
  setup() {
    return { v$: useVuelidate() };
  },
  data() {
    return {
      password: "",
      confirmPassword: '',
      secretWord: '',
      confirmSecretWord: '',
      firstName: "",
      lastName: "",
      telephone: "",
      photo: null,
      telegram: "",
      walletAddress: "",
      photoLink: null,
      videoLink: null,
      videoPreview: null,
      photoIsLoading: false,
      videoIsLoading: false,
    };
  },
  validations() {
    return {
      password: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      confirmPassword: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required), sameAsPassword: helpers.withMessage(localizeFilter('fields_not_match'), sameAs(this.password)) },
      secretWord: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      confirmSecretWord: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required), sameAsSecretWord: helpers.withMessage(localizeFilter('fields_not_match'), sameAs(this.secretWord)) },
      firstName: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      lastName: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      telephone: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      walletAddress: {
        required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
        minLength: helpers.withMessage(localizeFilter('min_length', [34]), minLength(34)),
        maxLength: helpers.withMessage(localizeFilter('max_length', [34]), maxLength(34)),
      },
    };
  },
  methods: {
    closeModal(reload) {
      this.$emit('closeModal', reload)
    },
    async submitForm() {
      const result = await this.v$.$validate();
      if (!result) {
        return
      }

      const data = {
        password: this.password,
        secretWord: this.secretWord,
        firstName: this.firstName,
        lastName: this.lastName,
        telephone: this.telephone,
        photo: this.photoLink,
        telegram: this.telegram,
        walletAddress: this.walletAddress,
        video: this.videoLink,
      }

      try {
        await this.$store.dispatch('uploadAdditionalInfo', data)
        this.$message('your_data_was_sent_for_verification')
        this.$emit('closeModal')
      }
      catch (e) {
        console.log(e)
      }
      finally {
        this.$emit('closeModal', true)
      }
    },
    async uploadPhoto(e) {
      const file = e.target.files[0];
      this.photoIsLoading = true
      try {
        let data = await api.uploadPhoto(e.target.files[0])
        this.photoLink = data.data
      } catch (e) {
        console.log(e);
      } finally {
        this.$refs.inputPhoto.value = null;
        this.photoIsLoading = false
      }
    },

    deletePhoto() {
      this.photoLink = null;
      this.$refs.inputPhoto.value = null;
    },

    deleteVideo() {
      this.videoLink = null;
      this.videoPreview = null;
      this.$refs.inputVideo.value = null;
    },

    async uploadVideo(e) {
      const file = e.target.files[0];
      this.videoIsLoading = true

      try {
        let data = await api.uploadVideo(e.target.files[0])
        // this.videoLink = data.data
        this.videoPreview = await generateVideoPreview(e.target.files[0])
      } catch (e) {
        console.log(e);
      } finally {
        this.$refs.inputVideo.value = null;
        this.videoIsLoading = false
      }
    },
  },
})
</script>

