<template>
  <div>
	<GiftBriefing v-if="showGiftBriefing" :state="state" @closeModal="showActiveBriefing = false" @click="showModal" />

    <GiveawayModal v-if="showGiveawayModal" @participate="participate" @closeModal="showGiveawayModal = false"/>
	<EnterPromoCodeModal v-if="showEnterPromoCodeModal" @enterCode="enterCode" @closeModal="showEnterPromoCodeModal = false"/>

    <CodeWrong v-if="showCodeWrong" @closeModal="showCodeWrong = false" />
    <CodeActivated v-if="showCodeActivated" @closeModal="showCodeActivated = false" />
  </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import GiftBriefing from '@/components/newModals/GiftModule/GiftBriefing.vue'
import GiveawayModal from '../components/newModals/GiftModule/GiveawayModal.vue'
import EnterPromoCodeModal from '../components/newModals/GiftModule/EnterPromoCodeModal.vue'
import CodeWrong from '@/components/newModals/GiftModule/CodeWrong.vue'
import CodeActivated from '@/components/newModals/GiftModule/CodeActivated.vue'

export default defineComponent({
	components: {
		GiftBriefing,
		GiveawayModal,
		EnterPromoCodeModal,
		CodeWrong,
		CodeActivated,
	},
	data() {
		return {
			showGiftBriefing: true,
			showGiveawayModal: false,
			showEnterPromoCodeModal: false,
			showCodeWrong: false,
			showCodeActivated: false,

			state: 'activeBriefing' // activeBriefing, timeToAct, finishedBriefing
		}
	},
	methods: {
		showModal() {
			if (this.state === 'activeBriefing') {
				this.showGiveawayModal = true
			} else if (this.state === 'timeToAct') {
				this.showEnterPromoCodeModal = true
			}
		},
		participate() {
			this.showGiveawayModal = false
			this.showEnterPromoCodeModal = true
		},
		enterCode(code) {
			this.showEnterPromoCodeModal = false
			// ...
			// server request
			const codeIsCorrect = false
			if (codeIsCorrect) {
				this.showCodeActivated = true
			} else {
				this.showCodeWrong = true
			}
		}
	}
})
</script>

