<template>
    <div class="modal-layout">
    <div class="darken" @click="close()">
    <div class="new-modal" @click.stop="">
        <button class="close-btn orange-cross" @click="close"></button>

        <div class="cover">
            <p class="text" v-html="$localizeFilter('watch_our_platform_reviews')"></p>
        </div>

        <div class = "modal-content">
            <div id="platformReviewsModalContentWidthAnchor"></div>

            <iframe
              ref="platformReviewsVideoPlayer"
              id="platformReviewsVideoPlayer"
              class="video"
              title="YouTube video player"
              frameborder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              allowfullscreen
              :width="width"
              :height="width/2"
              :src="fullLink"> 

            ></iframe>
        </div>
    </div>
    </div>
    </div> 
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import { toHandlerKey } from 'vue';
import YouTube from "vue3-youtube";

export default defineComponent({
    components: {
        YouTube
    },
    setup() {
        
    },
    data: ()=>({
        width: 845,
        links: ['_sUQPF90S94', 'hojboK6pWfY', 'OOL1Sg8K--k'],
        unwatchedLinks: []
    }),
    computed: {
        frameWidth() {
            return Math.min(Math.min(window.innerWidth, window.screen.availWidth) - 40, 1200);
        },
        frameHeight() {
            return this.frameWidth * 0.5625;
        },
        locale() {
            return this.$store.getters.selectedLocale
        },
        fullLink() {
            return `https://www.youtube.com/embed/` + this.unwatchedLinks[0] + `?loop=1&enablejsapi=1&playlist=` + this.unwatchedLinks.join(',')
        },
        providingLink() { 
            return this.locale === 'en' ? 'yVzRFmGFtJc' : 'jV_Cw4lIklQ'
        },
        priorityLink() {
            return '_sUQPF90S94'
        }
    },
    methods: {
        async close() {
            this.$emit('close')
        },
    },
    mounted(){
        this.width = document.getElementById('platformReviewsModalContentWidthAnchor').offsetWidth;

        const watchedLinks = localStorage.getItem('watchedLinks');
        if (watchedLinks) {
            this.unwatchedLinks = this.links.filter(link => !watchedLinks.split(',').includes(link));
        } else {
            this.unwatchedLinks = this.links;
        }

        if (this.unwatchedLinks.length > 1) {
            localStorage.setItem('watchedLinks', watchedLinks ? watchedLinks + ',' + this.unwatchedLinks[0] : this.unwatchedLinks[0]);
        } else {
            localStorage.removeItem('watchedLinks');
        }

        this.unwatchedLinks.push(this.providingLink)
        const pos = Math.floor(Math.random() * this.unwatchedLinks.length)
        console.log(pos, this.unwatchedLinks.length)
        this.unwatchedLinks.splice(pos, 0, this.priorityLink)
    }
})
</script>


