<template>
	<div class="modal-layout">
		<div class="darken" @click="closeModal">
			<div class="new-modal" @click.stop="">
				<div class="new-modal-back-button-container">
					<button class="new-modal-back-button" @click="closeModal"></button>
				</div>
				<div class="modal-content">
					<div class="title">{{ type == 'cryptostart' ? $localizeFilter('crypto_starter_modal_title') : $localizeFilter('game_modal_title') }}</div>
					<div class="wrapper">

						<ul class="payment">
							<li class="payment-item" v-for="(pool, idx) in pools" :key="idx">
								<div class="conditions-block" @click="setSelected(idx)">
									<input type="checkbox" :id="pool.id" class="custom-checkbox" name="pool"
										:checked="pool?.selected || false">
									<label class="conditions-label">{{ pool.name }}</label>
								</div>
								<div :class="!pool.selected ? 'payment-item-balance text-inactive' : 'payment-item-balance'">{{
									pool.selected ? $localizeFilter('chosen') : $localizeFilter('on_account') }}: {{ pool.selected ?
		formatUSDT(pool.value) : formatUSDT(pool.balance) }} USDT
								</div>
								<div :class="!pool.selected ? 'payment-item-coefficient text-inactive' : 'payment-item-coefficient'">
									{{ pool.coefficient }}</div>
								<div class="payment-item-quantity slidercontainer">
									<div :class="!pool.selected ? 'min-value text-inactive' : 'min-value'">0</div>
									<div :class="!pool.selected ? 'max-value text-inactive' : 'max-value'">{{
										formatUSDT(pool.balance) }}</div>
									<input :class="!pool.selected ? 'inactive slider' : 'slider'" type="range" min="0" v-model="pool.value"
										step="10000" :max="pool.balance" id="rangeSlider">
								</div>
							</li>
						</ul>
						<div class="share-wrapper">
							<p class="new-modal-text">{{ $localizeFilter('amount_share') }}</p>
							<label class="pay-value">{{ formatUSDT(totalValue) }} USDT</label>
						</div>
					</div>


					<div class="new-modal-content">
						<label class="new-modal-text">{{ $localizeFilter('enter_share_via', ['FENIX']) }}</label>
						<input v-model="result" :placeholder="$localizeFilter('amount_in', ['FENIX'])" type="number"
							class="new-modal-input">
						<!-- :class="{ invalid: v$.result.$error } -->
						<!-- <small class="error" v-for="(error, index) of v$.result.$errors" :key="index">
						{{ error.$message }}
					</small> -->
					</div>




					<div :class="totalValue < 1000000000 ? 'disabled' : ''" class="new-modal-content">
						<div class="conditions-block" @click="acceptShoulder">
							<input type="checkbox" id="checkbox" class="custom-checkbox" v-model="shoulderAccepted"
								@click="acceptShoulder">
							<label for="checkbox" class="conditions-label">{{ type == 'cryptostart' ? $localizeFilter('shoulder5x') : $localizeFilter('shoulder2x')}}</label>
						</div>

						<label class="new-modal-text">{{ $localizeFilter('available_credit_limit') }}</label>
						<div class="data-info new-modal-input">{{ formatUSDT(totalValue < 1000000000 ? 0 : avaivableCreditLimit)
						}}</div>

								<div class="modal-warning">
									<img class="warning-label" src="../../assets/newImages/warning-label.svg" alt="">
									<p class="window-text warning">{{ $localizeFilter('credit_limit_hint') }}</p>
								</div>


								<label class="new-modal-text">{{ $localizeFilter('total_amount') }}</label>
								<div class="data-info new-modal-input">{{ formatUSDT(totalValueWithCreditLever) }}</div>
						</div>



						<div class="info">{{ $localizeFilter(type == 'cryptostart' ? 'result_crypto_start2' : 'result_game2', [formatPercent(getPercent)]) }}</div>


						<button :class="totalValueWithCreditLever ? 'basic-button pay-active' : 'basic-button'" @click="pay">{{
							$localizeFilter('enter_share')
						}}</button>


					</div>
				</div>
			</div>
		</div>
</template>


<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
	props: {
		type: {
			type: String,
			required: true // cryptostart, game
		},
	},
	methods: {
		closeModal() {
			this.$emit('closeModal')
		},
		setSelected(idx) {
			if (this.pools[idx].selected === false) {
				this.pools[idx].selected = !this.pools[idx].selected
				this.pools[idx].value = this.pools[idx].balance
			} else {
				this.pools[idx].selected = !this.pools[idx].selected
				this.pools[idx].value = 0
			}
		},
		formatUSDT(value) {
			return (value / 1000000).toFixed(2)
		},
		formatPercent(value) {
			return (value).toFixed(8)
		},
		pay() {
			if (this.fenixValue) {
				const selectedPools = this.pools.filter(pool => pool.selected)
				this.$emit('payPools', {
					selectedPools,
					fenixAmount: this.fenixValue / 1000000,
					product: this.type
				})
			}
		},
	},
	computed: {
		poolIsSelected() {
			return this.pools.some(pool => pool.selected)
		},
		totalValue() {
			return this.pools.reduce((acc, pool) => acc + Number(pool.value * pool.coefficient || 0), 0)
		},

		fenixValue() {
			return +this.result * 1000000
		},

		creditCoefficient() {
			return this.type == 'cryptostart' ? 4 : 1
		},

		avaivableCreditLimit() {
			return this.totalValue * this.creditCoefficient
		},

		totalValueWithCreditLever() {
			// return this.totalValue > this.willLeftInPools ? this.willLeftInPools * 4 + this.totalValue : this.totalValue * 5
			return this.fenixValue >= 1000000000 ? Math.min(this.totalValue, this.fenixValue) * this.creditCoefficient + this.fenixValue : this.fenixValue
		},

		getPercent() {
			if (this.type === 'cryptostart') {
				return this.totalValueWithCreditLever / 1000000 / 100000000 * 100
			}
			return this.totalValueWithCreditLever / 1000000 / 75000000 * 100
		},



	},
	data() {
		return {
			pools: [],
			shoulderAccepted: true,

			totalAmount: 0,
			creditLimit: 0,

			result: '',
		}
	},
	async mounted() {
		if (this.type === 'cryptostart') {
			try {
				const response = await this.$store.dispatch('getCryptoStartAvailablePools')
				this.pools = response
				for (let i = 0; i < this.pools.length; i++) {
					this.pools[i].value = 0
					this.pools[i].selected = false
				}
			} catch (e) {
				console.log(e)
			}
		} else if (this.type === 'game') {
			try {
				const response = await this.$store.dispatch('getGameAvailablePools')
				this.pools = response
				for (let i = 0; i < this.pools.length; i++) {
					this.pools[i].value = 0
					this.pools[i].selected = false
				}
			} catch (e) {
				console.log(e)
			}
		}
		
	},
}
)
</script>

