<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
	data: () => ({
		fenixBalance: 0,
	}),
	methods: {
		closeModal() {
			this.$emit('closeModal')
		},
		selectMethod(method) {
			this.$emit('paymentMethodSelected', method)
		},
		async loadData() {
			try {
				const data = await this.$store.dispatch('getBalance', { currencyCode: 'FENIX' })
				this.fenixBalance = data.balance.toFixed(2)
				this.$emit('setBalance', this.balance)
			} catch (e) {
				this.$error(e)
			}
		}
	},
	mounted() {
		this.loadData()
	},
}
)
