<template>
	<div class="modal-layout">
		<div class="darken" @click="closeModal">
			<div class="new-modal" @click.stop="">
				
				<div class="new-modal-back-button-container">
					<button class="new-modal-back-button" @click="closeModal"></button>
				</div>

				<div class="modal-content">
					<div class="title">{{ $localizeFilter('promo_code_modal_title') }}</div>
					<div class="сountdown"><span>{{resultHours}}</span>:<span>{{resultMinutes}}</span>:<span>{{resultSeconds}}</span></div>

					<input class="input" type="text" :placeholder="$localizeFilter('promo_code_modal_placeholder').toUpperCase()" v-model="code"
					@change="v$.code.$touch" :class="{'error': v$.code.$error}" >
					<span v-for="error in v$.code.$errors" :key="error.$uid" class="label-error">
						{{ error.$message }}
					</span>

					<a class="link" target="_blank" :href="telegramChat">{{link}}</a>
					<div class="button" @click="enterCode">{{ $localizeFilter('enter_the_code') }}</div>
				</div>

			</div>
		</div>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from '@vuelidate/core';
import { required, numeric, helpers } from '@vuelidate/validators';
import localizeFilter from '@/filters/localize.filter'
import { ref, computed, onMounted, watchEffect } from 'vue';
import { useTimer } from 'vue-timer-hook';


export default defineComponent({
   data: () => ({
		link: 'https://t.me/Capital_Whale',
		telegramChat: 'https://t.me/Capital_Whale',
		code: ''
  }),
  setup() {
	const time = new Date();
	time.setSeconds(time.getSeconds() + 300); // 5 minutes timer
	const timer = useTimer(time);
	// const restartFive = () => {
	// 	// Restarts to 5 minutes timer
	// 	const time = new Date();
	// 	time.setSeconds(time.getSeconds() + 300);
	// 	timer.restart(time);
	// }
	onMounted(() => {
	watchEffect(async () => {
		if(timer.isExpired.value) {
			console.warn('IsExpired')
		}
	})
	})

	const resultHours = computed(() => {
		return timer.hours.value < 10 ? `0${timer.hours.value}` : timer.hours.value;
	});

	const resultMinutes = computed(() => {
		return timer.minutes.value < 10 ? `0${timer.minutes.value}` : timer.minutes.value;
	});

	const resultSeconds = computed(() => {
		return timer.seconds.value < 10 ? `0${timer.seconds.value}` : timer.seconds.value;
	});

    return { v$: useVuelidate(), resultHours, resultMinutes, resultSeconds, timer };
  },
  validations() {
	return {
		code: {
			required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
			numeric: helpers.withMessage(localizeFilter('field_must_be_numeric'), numeric),
		}
	}
  },
  methods: {
	closeModal() {
		this.$emit('closeModal')
	},
	enterCode() {
		this.v$.$touch()
		if (this.v$.code.$error) {
			return
		}
		this.$emit('enterCode', this.code)
	},
  }
}
)
</script>

