import { api } from '@/plugins/api'

export default {
    state: {
      showAdditionalInfoModal: false,
    },
    getters: {
      showAdditionalInfoModal: (state, getters) => {
        return state.showAdditionalInfoModal
      }
    },
    actions: {
      async register ({ dispatch, commit }, {email, firstName, lastName, sponsorEmail, password,  buyCoinTicker}) {
          try {
            await api.register( {email, firstName, lastName, sponsorEmail, password, buyCoinTicker})
            commit('SET_LOGGINED', true)
          } catch (e) {
            commit('SET_ERROR', e)
            throw e;
          }
        },

        async login ({ dispatch, commit }, {email, password, buyCoinTicker}, ctx) {
          try {
            await api.login({email, password, buyCoinTicker})
            commit('SET_LOGGINED', true)
          } catch (e) {
            commit('SET_ERROR', e)
            throw e;
          }
        },

        async requestRestorePassword ({ dispatch, commit }, {email}) {
          try {
            await api.requestRestorePassword({email})
          } catch (e) {
            commit('SET_ERROR', e)
            throw e;
          }
        },

        async restorePassword ({ dispatch, commit }, {newPassword, code}) {
          try {
            await api.restorePassword({newPassword, code})
          } catch (e) {
            commit('SET_ERROR', e)
            throw e;
          }
        },

        async activateEmail ({ dispatch, commit }, {token}) {
          try {
            let {token: sessionId, bonus: bonus} = await api.activateEmail({token})
            localStorage.exchangeClientSession = sessionId;
            localStorage.exchangeWelcomeBonusData = JSON.stringify(bonus)
            api.setCookie(sessionId);
          } catch (e) {
            commit('SET_ERROR', e)
            throw e;
          }
        },

        async getEmailByReferralToken ({ dispatch, commit },{token}) {
          try {
            return await api.getEmailByReferralToken({token})
          } catch (e) {
            commit('SET_ERROR', e)
            throw e;
          }
        },

        async logout ({ dispatch, commit }) {
          try {
            await api.logout({})
            commit('SET_LOGGINED', false)
          } catch (e) {
            commit('SET_ERROR', e)
            throw e;
          }
        },

        async uploadAdditionalInfo({dispatch, commit}, {  password, secretWord, firstName, lastName, telephone, photo, telegram, walletAddress, video }) {
          try {
            await api.uploadAdditionalInfo( {  password, secretWord, firstName, lastName, telephone, photo, telegram, walletAddress, video })
          } catch (e) {
            commit('SET_ERROR', e)
            throw e;
          }
        },

        async setShowAdditionalInfoModal({commit}, {value}) {
          commit('SET_SHOW_ADDITIONAL_INFO_MODAL', value)
        }
    },
    
    mutations: {
      SET_SHOW_ADDITIONAL_INFO_MODAL(state, data) {
        state.showAdditionalInfoModal = data
      }
    }
    
}