<template>
    <div>
        <transition-group name="fade" mode="in-out">
            <!-- <ChoosePaymentMethodModal v-if="showChoosePaymentMethodModal" @paymentMethodSelected="paymentMethodSelected" @closeModal="closeModals"/> -->

            <ChoosePaymentMethodModal2 v-if="showChoosePaymentMethodModal"
                @paymentMethodSelected="paymentMethodSelected" @closeModal="closeModals" />

            <PaymentWithPoolsModal v-if="showPaymentWithPoolsModal" :type="'game'" @payPools="payPools" @closeModal="closeModals" />

            <!-- <CryptoStartFenixModal v-if="showFenixModal" @payFenix="payFenix" @closeModal="closeModals" /> -->

             <TransferModal class="transfer-modal" v-if="showTransferModal" :isGame="true" :amount="transferData?.amount" :transferData="transferData" @closeModal="closeModals"/>

        <!-- <InfoModal class="continue-payment-modal" v-if="showContinuePaymentModal" :view="'full'" :text="continuePaymentCryptoStartModalText" :button="{title: $localizeFilter('continue'), action: continuePayment}" @closeModal="closeModals"/>

        <PaymentSuccessfulModal class="payment-successful-modal" v-if="showPaymentSuccessfulModal" :view="'center'" :text="$localizeFilter('level_payment_successful_alert_text', [paymentCryptoStartData.level])" :button="{title: 'Ok', action: closeModals}"/> -->

            <div class="darken" v-if="showLoader">
                <Loader v-if="showLoader" visible="true" />
            </div>
        </transition-group>

    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
// import ChoosePaymentMethodModal from '@/components/newModals/ChoosePaymentMethodModal.vue'
// import InfoModal from "@/components/newModals/InfoModal.vue"
// import PaymentSuccessfulModal from "@/components/newModals/PaymentSuccessfulModal.vue"
import Loader from "@/components/app/Loader.vue"
import localizeFilter from '@/filters/localize.filter'
import utils from '@/utils/utils'
import TransferModal from '../newModals/TransferModal.vue'
import ChoosePaymentMethodModal2 from '../newModals/ChoosePaymentMethodModal2.vue'
import PaymentWithPoolsModal from '../newModals/PaymentWithPoolsModal.vue'
// import CryptoStartFenixModal from '../newModals/CryptoStartFenixModal.vue'


export default defineComponent({
    setup() {

    },
    components: {
        // InfoModal,
        // PaymentSuccessfulModal,
        // ChoosePaymentMethodModal,
        Loader,
        TransferModal,
        ChoosePaymentMethodModal2,
        PaymentWithPoolsModal,
        // CryptoStartFenixModal
    },
    data: () => ({
        showChoosePaymentMethodModal: true,
        showPaymentWithPoolsModal: false,
        showFenixModal: false,
        showTransferModal: false,
        // showContinuePaymentModal: false,
        // showPaymentSuccessfulModal: false,
        showLoader: false,
        selectedMethod: null,
        transferData: null,
    }),
    computed: {
        // paymentLevel() {
        //     return this.$store.getters.paymentLevel
        // }
        continuePaymentCryptoStartModalText() {
            return this.$store.getters.continuePaymentCryptoStartModalText
        },
        paymentCryptoStartData() {
            return this.$store.getters.paymentCryptoStartData
        },
        paymentCryptoStartAmount() {
            return this.$store.getters.paymentCryptoStartAmount
        },
    },
    methods: {
        async payPools({selectedPools: pools, fenixAmount}) {
            try {
                pools = pools.map(pool => {
                    return {
                        poolId: pool.id,
                        amount: +pool.value
                    }
                })
                const formData = { pools, method: this.selectedMethod, fenixAmount }
                const result = await this.$store.dispatch('buyGameShare', formData)
                if (result.transferData) {
                    this.transferData = result.transferData
                    this.showPaymentWithPoolsModal = false
                    this.$nextTick(()=>{
                        // this.showLoader = false
                        this.showTransferModal = true
                    })
                } else {
                    this.$message(localizeFilter('game_share_bought_successfully'))
                    await this.$store.dispatch('getProfile', {})
                    this.closeModals()
                }
            } catch (e) {
                this.$error(e)
            }
        },
        // async payFenix(amount) {
        //     try {
        //         await this.$store.dispatch('buyCryptoStartShare', { fenixAmount: amount })
        //         this.$message(localizeFilter('crypto_start_share_bought_successfully'))
        //         await this.$store.dispatch('getProfile', {})
        //         this.closeModals()
        //     } catch (e) {
        //         this.$error(e)
        //     }
        // },
        async paymentMethodSelected(method) {
            this.showChoosePaymentMethodModal = false
            this.$nextTick(() => {
                this.selectedMethod = method
                // if (method === 'fenix') {
                //     this.showFenixModal = true
                // } else if (method === 'pool') {
                //     this.showPaymentWithPoolsModal = true
                // }
                this.showPaymentWithPoolsModal = true
            })
        },

        continuePayment() {
            this.$nextTick(() => {
                window.open(this.paymentCryptoStartData.redirectTo, '_blank').focus()
                // this.showContinuePaymentModal = false
                this.showLoader = true
            })
        },
        paymentSuccessful() { // TODO: call this method from redirect
            this.$nextTick(() => {
                this.showLoader = false
                this.showPaymentSuccessfulModal = true
                this.clearAll()
            })
        },
        closeModals() {
            this.showChoosePaymentMethodModal = false
            this.showPaymentWithPoolsModal = false
            this.showFenixModal = false
            // this.showContinuePaymentModal = false
            // this.showPaymentSuccessfulModal = false
            this.showTransferModal = false
            this.$emit('close')
            this.clearAll()
        },
        async clearAll() {
            await this.$store.dispatch('clearPaymentCryptoStartData')
        }
    },
    async mounted() {
    }
})
</script>

<style scoped></style>